<template>
    <div>
        <label>
            <div class="button top">Add New File</div>
            <input
                style="display: none"
                type="file"
                multiple
                :name="uploadFieldName"
                :disabled="isSaving"
                @change="
                    filesChange($event.target.name, $event.target.files);
                    fileCount = $event.target.files.length;
                "
                accept="image/*"
                class="input-file"
            />
        </label>
        <monitoring-uploads
            class="monitoring-uploads"
            :uploading="uploading"
            :times="times"
            :sizes="sizes"
        />
        <div
            v-for="(file, i) in files.slice().reverse()"
            :key="i"
            class="files-box"
        >
            <span @click="openLink(file[0])" class="files">{{ file[0] }}</span>
            <span class="file-actions">
                <!--<span @click="rename(file[0])">přejmenuj</span>-->
                <span @click="set_privacy(file[0], 'private')">skryj</span>
                <span @click="remove(file[0])">smaž</span>
            </span>
            <div class="details">{{ file }}</div>
        </div>
        <div v-if="Object.keys(urls).length > 0">
            <h4>Private files</h4>
            <ol>
                <li
                    v-for="(url, n) in urls"
                    :key="url"
                    :id="url"
                    onclick="window.open(this.id)"
                    class="files"
                >
                    {{ n }}
                </li>
            </ol>
        </div>
        <div v-if="Object.keys(urls_e).length > 0">
            <h4>Errored private files</h4>
            <ol>
                <li v-for="(code, n) in urls_e" :key="code">
                    {{ n }}: <b>{{ code }}</b>
                </li>
            </ol>
        </div>
        <monitoring-uploads
            class="monitoring-uploads"
            :uploading="uploading"
            :times="times"
            :sizes="sizes"
        />
        <!--<input type="file" id="input" multiple @change="manage_upload()" />-->
        <div class="container" style="display: none">
            <!--UPLOAD-->
            <form
                enctype="multipart/form-data"
                novalidate
                xxxv-if="isInitial || isSaving"
            >
                <!--<h1>Upload images</h1>-->
                <div class="dropbox">
                    <input
                        type="file"
                        multiple
                        :name="uploadFieldName"
                        :disabled="isSaving"
                        @change="
                            filesChange(
                                $event.target.name,
                                $event.target.files
                            );
                            fileCount = $event.target.files.length;
                        "
                        accept="image/*"
                        class="input-file"
                    />
                    <p xxxv-if="isInitial">
                        Drag your file(s) here<br />
                        or click to upload files
                    </p>
                    <p v-if="isSaving">Uploading {{ fileCount }} files...</p>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import "firebase/storage";
import monitoringUploads from "./monitoring-uploads";
//https://www.digitalocean.com/community/tutorials/how-to-handle-file-uploads-in-vue-2
const STATUS_INITIAL = 0,
    STATUS_SAVING = 1,
    STATUS_SUCCESS = 2,
    STATUS_FAILED = 3;
/*
name	"images/WP_20200916_14_47_25_Pro-gimp.jpg"
bucket	"games-in-action.appspot.com"
generation	"1606231342881197"
metageneration	"1"
contentType	"image/jpeg"
timeCreated	"2020-11-24T15:22:22.880Z"
updated	"2020-11-24T15:22:22.880Z"
storageClass	"STANDARD"
size	"7740088"
md5Hash	"JY2PdIqHWX7SINNsEsb8qQ=="
*/
export default {
    props: ["firebase"],
    components: { monitoringUploads },
    data() {
        return {
            storageRef: this.firebase.storage().ref(),
            files: [],
            urls: {},
            urls_e: {},
            uploading: {},
            times: {},
            sizes: {},

            uploadedFiles: [],
            uploadError: null,
            currentStatus: null,
            uploadFieldName: "photos",
        };
    },
    computed: {
        isInitial() {
            return this.currentStatus === STATUS_INITIAL;
        },
        isSaving() {
            return this.currentStatus === STATUS_SAVING;
        },
        isSuccess() {
            return this.currentStatus === STATUS_SUCCESS;
        },
        isFailed() {
            return this.currentStatus === STATUS_FAILED;
        },
    },
    methods: {
        updateJSONreactivelly(object, updatedData) {
            //nejde
            object = Object.assign({}, object, updatedData);
        },
        openLink(url) {
            this.$w.open(
                "https://firebasestorage.googleapis.com/v0/b/games-in-action.appspot.com/o/images%2F" +
                    //this.innerHTML.replace(/\s+/g, '') +
                    url +
                    "?alt=media"
            );
        },
        monitoringUpload(filename, ratio) {
            var n = {};
            n[filename] = ratio;
            // nejde this.updateJSONreactivelly(this.uploading, n);
            this.uploading = Object.assign({}, this.uploading, n);
            console.log(this.uploading);
        },
        fb_upload(file, filename, uploadMonitor) {
            if (typeof filename == "undefined" || !filename)
                filename = file.name;
            var fileRef = this.storageRef.child("images/" + filename);

            var start_time = new Date().getTime() / 1000;
            let uploadTask = fileRef.put(file);
            this.$w.console.log(file);
            this.sizes[filename] = file.size || null;

            uploadTask.then((snapshot) => {
                console.log(
                    "Uploaded a blob or file!",
                    filename,
                    duration + "s"
                );
                let end_time = new Date().getTime() / 1000;
                let duration = end_time - start_time;
                this.$set(this.times, filename, {
                    start_time,
                    end_time,
                    duration,
                });
                this.files.push([filename]);
            });

            uploadTask.on("state_changed", function (s) {
                var ratio = s.bytesTransferred / s.totalBytes;
                console.log(s, ratio);
                if (typeof uploadMonitor === "function")
                    uploadMonitor(filename, ratio);
            });
        },
        rename(filename) {
            filename;
        },
        set_privacy(filename, privacy) {
            var fileRef = this.storageRef.child("images/" + filename);
            var metadata = {
                customMetadata: {
                    private: privacy === "private" ? "true" : null,
                },
            };
            // Update metadata properties
            fileRef
                .updateMetadata(metadata)
                .then(function (metadata) {
                    // Updated metadata for 'images/forest.jpg' is returned in the Promise
                    alert("ok");
                })
                .catch(function (error) {
                    // Uh-oh, an error occurred!
                    alert("error");
                    console.log(error);
                });
        },
        remove(filename) {
            var fileRef = this.storageRef.child("images/" + filename);
            fileRef
                .delete()
                .then(() => {
                    // File deleted successfully
                    // https://stackoverflow.com/questions/3954438/how-to-remove-item-from-array-by-value
                    //this.files[filename] = null;
                    this.files.map((v, index) => {
                        if (v.indexOf(filename) > -1)
                            this.files.splice(index, 1);
                    });
                    /** postaru 
                    var index = this.files.indexOf(filename);
                    
                    if (index !== -1) {
                        this.files.splice(index, 1);
                    }
                    */
                    console.log('"' + filename + '" deleted.');
                })
                .catch(function (error) {
                    // Uh-oh, an error occurred!
                    console.log(error);
                    alert("error");
                });
        },
        reset() {
            // reset form to initial state
            this.currentStatus = STATUS_INITIAL;
            this.uploadedFiles = [];
            this.uploadError = null;
        },
        save(formData) {
            this.$w.console.log(formData);
            return;
            // upload data to the server
            this.currentStatus = STATUS_SAVING;

            upload(formData)
                .then((x) => {
                    this.uploadedFiles = [].concat(x);
                    this.currentStatus = STATUS_SUCCESS;
                })
                .catch((err) => {
                    this.uploadError = err.response;
                    this.currentStatus = STATUS_FAILED;
                });
        },
        filesChange(fieldName, fileList) {
            for (let i = 0; i < fileList.length; i++) {
                this.fb_upload(fileList[i], null, this.monitoringUpload);
            }
            return;
            // handle file changes
            if (!fileList.length) return;
            const formData = new FormData();

            // append the files to FormData
            Array.from(Array(fileList.length).keys()).map((x) => {
                formData.append(fieldName, fileList[x], fileList[x].name);
            });

            // save it
            this.save(formData);
        },
        show_files() {
            // Find all the prefixes and items.
            this.storageRef
                .child("images/")
                .listAll()
                .then((res) => {
                    res.prefixes.forEach(function (folderRef) {
                        // All the prefixes under listRef.
                        // You may call listAll() recursively on them.
                    });
                    res.items.forEach((itemRef) => {
                        window["x"] = itemRef;
                        console.log(itemRef);
                        // All the items under listRef.
                        this.files.push([
                            itemRef.name,
                            itemRef.path,
                            itemRef.customMetadata,
                            Object.keys(itemRef.storage.app),
                            itemRef.size,
                            itemRef.storage.contentType,
                            itemRef.storage.app.name,
                        ]);
                        let is_private = true;
                        if (is_private)
                            itemRef
                                .getDownloadURL()
                                .then((url) => {
                                    let n = {};
                                    n[itemRef.name] = url;
                                    this.urls = Object.assign({}, this.urls, n);
                                })
                                .catch((e) => {
                                    let n = {};
                                    n[itemRef.name] = e.code;
                                    this.urls_e = Object.assign(
                                        {},
                                        this.urls_e,
                                        n
                                    );
                                    console.log("download error", itemRef, e);
                                });
                    });
                })
                .catch((error) => {
                    // Uh-oh, an error occurred!
                    console.log(error);
                });
        },
    },
    mounted() {
        this.reset();
        this.show_files();
    },
};
</script>

