<template>
    <div id="documents">
        <!--{{shown_note_i!==null?reversed_notes[shown_note_i].text:""}}-->
        <span
            v-if="
                shown_note_i != null &&
                note_name === null &&
                reversed_notes[shown_note_i].text !=
                    reversed_notes[shown_note_i].text_author
            "
            class="right2 button"
            title="autorův historický rukopis"
            @click="history = history ? '' : '_author'"
        >
            <svg width="14" height="14">
                <use xlink:href="#icon-sand-clock"></use>
            </svg>
        </span>
        <span
            v-if="shown_note_i != null && note_name === null"
            class="left button"
            title="uprav"
            @click="uprav"
        >
            <svg width="14" height="14">
                <use xlink:href="#icon-pen3"></use>
            </svg>
        </span>
        <span
            v-else-if="note_math && note_name !== null"
            class="left button"
            title="math"
        >
            <svg width="14" height="14">
                <use xlink:href="#icon-math"></use>
            </svg>
        </span>
        <span
            v-if="shown_note_i !== null || note_name"
            class="close button"
            title="close"
            @click="close_note"
        >
            <svg width="14" height="14">
                <use xlink:href="#icon-close"></use>
            </svg>
        </span>
        <span class="l-nav" v-if="!note_name && shown_note_i === null"
            ><span
                v-if="!show_files"
                @click="
                    show_files = true;
                    $emit('nav-status', !show_files);
                "
                >Soubory</span
            ><span
                v-else
                @click="
                    show_files = false;
                    $emit('nav-status', !show_files);
                "
                >Zpět</span
            ></span
        >
        <files v-if="show_files" :firebase="firebase" />
        <div id="writting" v-else>
            <div
                id="shown_note"
                class="box"
                v-if="shown_note_i != null && note_name === null"
            >
                <div class="name">
                    {{ reversed_notes[shown_note_i]["name" + history] }}
                    <span
                        class="people"
                        title="Autor"
                        @click="
                            $emit('p', reversed_notes[shown_note_i].author.uid)
                        "
                        >{{
                            (t = reversed_notes[shown_note_i]) && t.author
                                ? "(" +
                                  ((n = t.author.displayName)
                                      ? n
                                      : t.author.email) +
                                  ")"
                                : ""
                        }}</span
                    >
                    <span
                        class="people"
                        @click="$emit('p', i)"
                        v-for="(e, i) in reversed_notes[shown_note_i].editors"
                        :key="i"
                    >
                        {{ e.displayName || e.email }}
                    </span>
                    <span class="tags">
                        <span v-for="t in tags" :key="t">{{ t }}</span>
                    </span>
                    <span v-if="history"
                        ><svg width="18" height="18" fill="lightgray">
                            <use xlink:href="#icon-sand-clock"></use></svg
                    ></span>
                    <span
                        class="left2 button"
                        title="nastavení soukromí"
                        @click="change_privacy"
                    >
                        <svg width="14" height="14">
                            <use
                                :xlink:href="
                                    '#icon-' +
                                    (reversed_notes[shown_note_i].is_public
                                        ? 'public'
                                        : 'private')
                                "
                            ></use>
                        </svg>
                    </span>

                    <span
                        v-if="
                            !reversed_notes[shown_note_i].author ||
                            reversed_notes[shown_note_i].author.uid == u.uid
                        "
                        class="delete button"
                        title="delete"
                        @click="
                            deleteel('notes', reversed_notes[shown_note_i].id); //predpokladam zpozdeni
                            deleteel(
                                'public_notes',
                                reversed_notes[shown_note_i].id
                            );
                            shown_note_id =
                                reversed_notes.length > 1
                                    ? reversed_notes[
                                          reversed_notes.length - 1 >
                                          shown_note_i
                                              ? shown_note_i + 1
                                              : shown_note_i - 1
                                      ].id
                                    : null;
                            /*shown_note_i--;
            if (shown_note_i < 0) shown_note_i = null;*/
                        "
                    >
                        <svg width="14" height="14" fill="red">
                            <use xlink:href="#icon-close"></use>
                        </svg>
                    </span>
                </div>
                <div v-if="reversed_notes[shown_note_i].is_public" class="url">
                    <div :class="'labelcenter' + cs">zkopírováno</div>
                    <input
                        :value="
                            'https://www.games-in-action.eu/notes/?id=' +
                            encodeURIComponent(reversed_notes[shown_note_i].id)
                        "
                        @focus="
                            cs = ' show';
                            $w.setTimeout(function () {
                                cs = '';
                            }, 1000);
                            var a =
                                $w.document
                                    .activeElement; /* stejne jako $event.target*/
                            a.select();
                            /*$event.target*/ a.setSelectionRange(0, 99999);
                            $w.document.execCommand('copy');
                        "
                    />
                    <input
                        v-model="note_url"
                        placeholder="/notes/<note_name>"
                        @blur="cursor = update_note_name()"
                        v-if="
                            true || note || (note_name && note_name.length > 2)
                        "
                    />
                    <input
                        v-model="tags_input"
                        placeholder="add tags to cathegorize"
                        @blur="update_tags()"
                        v-if="
                            true ||
                            shown_note_i ||
                            (note_name && note_name.length > 2)
                        "
                    />
                </div>
                <div
                    class="text"
                    v-html="
                        format_text(
                            reversed_notes[shown_note_i]['text' + history]
                        )
                    "
                    @dblclick="uprav"
                ></div>
            </div>
            <div
                v-show="
                    shown_note_i === null ||
                    (shown_note_i !== null &&
                        (note_name !== null || note !== null))
                "
            >
                <!--<h2>Poznámky</h2>-->
                <div class="w">
                    <input
                        title="Přidej poznámku"
                        :disabled="is_disabled"
                        v-model="note_name"
                        placeholder="název nové poznámky"
                        @keyup.enter="
                            focus() /*focus textarea if possible or nothing*/
                        "
                        @blur="
                            (!$event.relatedTarget ||
                                $event.relatedTarget.nodeName.toLowerCase() !=
                                    'textarea') &&
                                (shown_note_i != null
                                    ? update_note()
                                    : add_note())
                        "
                    />
                    <div v-if="is_disabled" class="disabled-screen">
                        <img
                            :src="
                                reversed_notes[shown_note_i].locked_by.photoURL
                            "
                            :title="
                                reversed_notes[shown_note_i].locked_by
                                    .displayName
                            "
                            @click="show_profile"
                        />
                    </div>
                </div>
                <div
                    class="w"
                    v-if="note || (note_name && note_name.length > 2)"
                >
                    <textarea
                        id="note"
                        :disabled="is_disabled"
                        placeholder="# hlavní nadpis, ## podnadpis, *tučně*, čára ---"
                        ref="textarea"
                        v-model="note"
                        @focus="unlock_note()"
                        @blur="
                            cursor = getCursorPos($event.target);
                            if (cursor === -1) cursor = null;
                            optimize_inputs();
                            (!$event.relatedTarget ||
                                $event.relatedTarget.nodeName.toLowerCase() !=
                                    'input') &&
                                /*delete describing[p.id];*/
                                (shown_note_i != null
                                    ? update_note()
                                    : add_note());
                            cursor = null;
                        "
                        @input="$w.cursor = getCursorPos($event.target)"
                        @keydown.tab="tab($event)"
                    ></textarea>
                </div>
                <div
                    class="preview"
                    v-html="format_text(note, true)"
                    v-if="!is_disabled"
                ></div>
                <div v-else class="preview">
                    Právě na tom pracuje
                    <span class="people" @click="show_profile"
                        >{{
                            reversed_notes[shown_note_i].locked_by.displayName
                        }}.</span
                    >
                </div>
            </div>
            <div v-show="shown_note_i === null && !note_name">
                <div>
                    <div
                        v-for="(n, i) in reversed_notes"
                        :key="n.id"
                        :id="n.id"
                        class="box note"
                        @click="
                            shown_note_id = reversed_notes[i].id;
                            note_url = reversed_notes[i].url;
                            note_tags = reversed_notes[i].tags;
                            tags_input = tags_input_init;
                            //neupdatuje se url, pokud ho nekdo jiny zmeni xxxxxxxxxxxxxxxxxxx chyba?
                            manageScroll();
                        "
                    >
                        <span class="name">{{ n.name }}</span>
                        <span class="description">{{
                            n.text
                                ? n.text.substring(0, 30).replace(/[*#]/g, "") +
                                  (n.text.length > 30 ? "..." : "")
                                : ""
                        }}</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import files from "./components/files";

export default {
    components: { files },
    watch: {
        reversed_notes: async function (n) {
            var rr = false;
            if (rr) return;

            if (
                this.shown_note_i === null ||
                /*!n[this.shown_note_i].locked_by||*/ (n[this.shown_note_i]
                    .locked_by &&
                    n[this.shown_note_i].locked_by.uid == this.u.uid)
            )
                return;
            this.$w.console.log("watcher");
            this.$w.console.log(n[this.shown_note_i].text);
            this.$w.console.log("---------------------------");
            this.$w.console.log(this.note);
            //if(n[this.shown_note_i].text==o[this.shown_note_i].text)return;
            //this.note===null pri onfocusu, hodnoty zde se nesmi prepisovat, jinak se rusi autosizez
            if (this.note === null || n[this.shown_note_i].text == this.note)
                return; //null pri focusu
            this.$w.console.log("---------Nastavuje se----------");
            this.note = n[this.shown_note_i].text;
            this.$w.console.log("---------note nastavena na----------");
            this.$w.console.log(this.note);
            this.$w.console.log(this.note);
            this.$nextTick(function () {
                this.simulateInput(this.$refs.textarea);
            }); //pokud se zmeni v-model
            //a zaroven provede input simulace, v-model se vrati do puvodni hodnoty
            //this.note_name=n[this.shown_note_i].name;
            /*var r=true;
      if(r)return;*/
            /*if(this.$refs.textarea)this.$nextTick(function () {
        this.$w.console.log("tick")
        this.simulateInput(this.$refs.textarea);
      })*/
        },
        shown_note_id: async function (i) {
            if (!i) return;
            this.$nextTick(function () {
                let w = this.$w.correct_katex_overflow;
                if (typeof w === "function") w();
            });
        },
        note: async function (i) {
            if (i || !this.shown_note_id) return;
            this.$nextTick(function () {
                let w = this.$w.correct_katex_overflow;
                if (typeof w === "function") w();
            });
        },
    },
    created() {
        var s = document.createElement("script");
        s.innerHTML =
            "function clicked(){var l=this.event.target.textContent;if(l.indexOf('http')===-1&&l.indexOf('ftp')===-1&&l[0]!='\/')l='http://'+l;window.open(l)}";
        document.head.appendChild(s);

        s = this.$w.document.createElement("script");
        this.$w.document.head.appendChild(s);
        s.src =
            "https://www.games-in-action.eu/notes/correct-katex-overflow.js";
    },
    /*
  https://forum.vuejs.org/t/document-activeelement-as-a-computed-value/15233
  created() {
  document.addEventListener('focusin', this.focusChanged)
},
beforeDestroy() {
  document.removeEventListener('focusin', this.focusChanged)
},
methods: {
  focusChanged (event) {
    const el = event.target
    // do something with the element.
  }
}
*/
    computed: {
        is_disabled: function () {
            var d;
            return (
                this.shown_note_i != null &&
                (d = this.reversed_notes[this.shown_note_i]) &&
                d.locked_by &&
                d.locked_by.uid != this.u.uid
            );
        },
        /*shown_note_i:{
      get:function(){      
        if(this.shown_note_id==null)return;
        for(var i=0;i<this.reversed_notes.length;i++){
          if(this.shown_note_id==this.reversed_notes[i].id){
            break;
          }
          return i;
        }
        return null;
      },
      /*set: function(){
        this.shown_note_id=(this.shown_note_i==null?null:this.reversed_notes[this.shown_note_i].id);
      }*/
        //}
        shown_note_i: {
            get: function () {
                var nalezeno = false;
                if (this.shown_note_id == null || !this.reversed_notes)
                    return null;
                for (var i = 0; i < this.reversed_notes.length; i++) {
                    if (this.shown_note_id == this.reversed_notes[i].id) {
                        nalezeno = 1;
                        break;
                    }
                }
                //if(!nalezeno)this.shown_note_id=null;
                return nalezeno ? i : null;
            },
            set: function () {
                //this.shown_note_id=(this.shown_note_i==null?null:this.reversed_notes[this.shown_note_i].id);
                // rekurziva nekonecna
                //if(this.shown_note_i===null)this.shown_note_id=null;
            },
        },
        tags: function () {
            var d;
            return this.shown_note_i != null &&
                (d = this.reversed_notes[this.shown_note_i]) &&
                d["tags"]
                ? Object.keys(d["tags"])
                : [];
        },
        tags_input_init: {
            get: function () {
                return this.tags.join(", ");
            },
            set: () => null,
        },
    },
    methods: {
        tab(e) {
            e.preventDefault();
            var c;
            var t = e.target;
            if ((c = this.getCursorPos(t)) != -1) {
                var v = t.value;
                t.value = v.substring(0, c.start) + "  " + v.substring(c.end);
                t.selectionStart = t.selectionEnd = c.start + 2;
                t.dispatchEvent(new Event("input"));
            }
        },
        uprav() {
            if (this.note_name !== null || this.shown_note_i === null) return;
            this.note_name = this.reversed_notes[this.shown_note_i].name;
            this.note = this.reversed_notes[this.shown_note_i].text;
            this.focus();
        },
        getCursorPos(input) {
            if ("selectionStart" in input) {
                return {
                    start: input.selectionStart,
                    end: input.selectionEnd,
                };
            } else if (input.createTextRange) {
                var sel = document.selection.createRange();
                if (sel.parentElement() === input) {
                    var rng = input.createTextRange();
                    rng.moveToBookmark(sel.getBookmark());
                    for (
                        var len = 0;
                        rng.compareEndPoints("EndToStart", rng) > 0;
                        rng.moveEnd("character", -1)
                    ) {
                        len++;
                    }
                    rng.setEndPoint("StartToStart", input.createTextRange());
                    for (
                        var pos = { start: 0, end: len };
                        rng.compareEndPoints("EndToStart", rng) > 0;
                        rng.moveEnd("character", -1)
                    ) {
                        pos.start++;
                        pos.end++;
                    }
                    return pos;
                }
            }
            return -1;
        },
        show_profile(w) {
            if (typeof w != "string") w = "locked_by";
            this.$emit("p", this.reversed_notes[this.shown_note_i][w].uid);
        },
        simulateInput(t) {
            if (t) {
                var evt = document.createEvent("Events");
                evt.initEvent("input", true, true);
                t.dispatchEvent(evt);
            }
        },
        unlock_note(l) {
            this.$w.console.log("locking this.shown_note_id=");
            this.$w.console.log(this.shown_note_id);
            if (!this.shown_note_id) return;
            l = l || false;
            this.$db
                .ref("notes/" + this.shown_note_id + "/locked_by")
                .set(l ? null : this.editor);
        },
        close_note() {
            this.$w.cursor = null;
            this.shown_note_id = null;
            this.manageScroll();
            this.close_writing();
        },
        manageScroll() {
            var Y = this.Y;
            var el = this.$w.document.body; //.getElementById("logged"); vzdy 0??
            el = this.$w;
            this.Y = el.scrollY;
            this.$w.console.log(this.Y);
            if (el.scroll) el.scroll(0, Y);
        },
        focus() {
            this.$nextTick(() => {
                //autosize(this.$refs[ref]);
                //this.$root.window.alert(45545)
                if (!this.$refs.textarea) return;
                this.$refs.textarea.focus();
                let c = this.reversed_notes[this.shown_note_i].cursor;
                if (c) this.$refs.textarea.selectionEnd = c;
            });
        },
        close_writing() {
            this.note_name = this.note = null;
            //dodelat, at se resetuje velikost textareay
            if (this.$refs.textarea)
                this.$refs.textarea.removeAttribute("style");
        },
        add_note() {
            if (!this.note) return;
            this.$db.ref("notes").push(
                Object.assign(this.newItem, {
                    url: this.note_url || null,
                    name: this.note_name,
                    text: this.note,
                    name_author: this.note_name,
                    text_author: this.note,
                })
            );
            this.close_writing();
        },
        change_privacy() {
            var n = this.reversed_notes[this.shown_note_i];
            var pr = !n.is_public;
            this.$db.ref("public_notes/" + n.id).set(
                pr
                    ? {
                          text: n.text,
                          name: n.name,
                          url: n.url || null,
                          tags: n.tags || null,
                      }
                    : null
            );
            this.$db.ref("notes/" + n.id + "/is_public").set(pr);
        },
        update_tags() {
            var n;
            if (
                !(
                    this.shown_note_i != null &&
                    this.reversed_notes &&
                    (n = this.reversed_notes[this.shown_note_i])
                )
            )
                return alert("error");
            var t = (this.tags_input = this.tags_input.replace(
                /[^a-zA-Z,0-9_]/g,
                ""
            ));
            this.tags_input = this.tags_input.replace(/,/g, ", ");
            var tags = {};
            if (t)
                t.split(",").forEach((x) => {
                    if (x) tags[x] = true;
                });
            this.$db.ref("notes/" + n.id + "/tags").set(tags);
            this.$db.ref("public_notes/" + n.id + "/tags").set(tags);
        },
        update_note_name() {
            let id = this.reversed_notes[this.shown_note_i].id;
            if (typeof id == "undefined" || !id) return;
            this.$w.console.log(
                "updating note(" + id + ") url: " + this.note_url
            );
            this.$db.ref("notes/" + id + "/url").set(this.note_url || null);
            this.$db
                .ref("public_notes/" + id + "/url")
                .set(this.note_url || null);
        },
        update_note() {
            this.$w.console.log("updating note");
            this.unlock_note(true);
            let id = this.reversed_notes[this.shown_note_i].id;
            let customObj = {}; //uklada autorovu verzi
            if (this.u.uid != this.reversed_notes[this.shown_note_i].author.uid)
                this.$db
                    .ref("notes/" + id + "/editors/" + this.u.uid)
                    .set(this.editor);
            else
                customObj = {
                    name_author: this.note_name || null,
                    text_author: this.note || null,
                };
            let noteObj = {
                url: this.note_url || null,
                tags: this.note_tags || null,
                name: this.note_name || null,
                text: this.note || null,
            };
            if (this.cursor) noteObj.cursor = this.cursor.end;
            this.$db
                .ref("notes/" + id)
                .update(Object.assign(this.updatedItem, noteObj, customObj));
            if (this.reversed_notes[this.shown_note_i].is_public)
                this.$db.ref("public_notes/" + id).set(noteObj);
            this.close_writing();
        },
        optimize_inputs() {
            this.note_name = this.optimize_text(this.note_name);
            this.note = this.optimize_text(this.note);
            this.note_url = this.optimize_text(this.note_url);
        },
        optimize_text(s) {
            if (!s) return s;
            var del = this.cut_out_between(s, "```");
            s = del[0];
            var cut_array = del[1];
            while (s.match(/((\n|^)[-#][^\n]+\r?\n)\r?\n-/)) {
                s = s.replace(/((\n|^)[-#*][^\n]+\r?\n)\r?\n-/g, "$1-");
            }
            var si = s
                .replace(/\r/g, "")
                .replace(/^\s+|\s+$/g, "")
                .replace(
                    /\b(http.*?=|)(https?%3A%2F%2F[^&\s]+)(&.+|)\b/g,
                    function (a) {
                        return decodeURIComponent(
                            a.match(/https?%3A%2F%2F[^&\s]+/)
                        );
                    }
                )
                .replace(/\bhttp.*?url=(https?[^&\s]+)(&.+|)\b/g, "$1")
                .replace(
                    /((\n|^)[-#*][^\n]+\r?\n\r?\n)(\r?\n\r?\n\r?\n|\r?\n\r?\n|\r?\n)/g,
                    "$1"
                )
                //.replace(/<[^\s][^>]*>/g, "")
                .split("\n");
            for (var i = 0; i < si.length; i++)
                si[i] = si[i].replace(/^\s+|\s+$/g, "");
            si = si.join("\n");
            if (cut_array.length) si = this.add_cuts(si, cut_array, "```");
            return si;
        },
    },
    data: function () {
        return {
            note: null,
            note_name: null,
            note_url: "",
            note_tags: {},
            note_math: true,
            //shown_note_i: null,
            history: "",
            shown_note_id: null,
            show_files: false,
            cs: "",
            Y: 0,
            cursor: null,
            tags_input: null,
        };
    },
    mounted() {
        let uprav = this.uprav;
        this.$w.addEventListener("keyup", function () {
            let n = this.event.target.nodeName;
            if (["TEXTAREA", "INPUT"].indexOf(n) > -1) return;
            let c = this.event.keyCode;
            if (c == 69 || c == 13) if (typeof uprav === "function") uprav();
        });
        this.autosize(this.$refs.textarea);
        //fb().then(f => {alert(f.init);this.start_firebase(f.init())});
        //this.t=Vue
        //import("./fi.js").then(f => this.start_firebase(f.init()));
    },
    updated() {
        this.$w.console.log("autosize event on updated");
        //this.simulateInput(this.$refs.textarea);
        //this.$nextTick(()=>this.simulateInput(this.$refs.textarea))
        //needed because of watcher---prirazuje znovu hodnotu pri onfocus..
        //.podruhe prizmenenem inputu jiz se neresizuje
        this.autosize(this.$refs.textarea); //pri v-if se objevi v kodu
    },
    props: [
        "notes",
        "reversed_notes",
        "autosize",
        "firebase",
        "$db",
        "newItem",
        "updatedItem",
        "deleteel",
        "u",
        "editor",
        "format_text",
        "add_cuts",
        "cut_out_between",
    ],
};
</script>
<style reason="katex-bug-correction">
.katex-html {
    position: relative;
    padding-top: 1px;
    overflow: hidden;
}
