<template>
    <div
        id="app"
        v-if="u != '?'"
        @keyup.esc="
            profile = null;
            if ($refs.doc) $refs.doc.close_note();
        "
        tabindex="0"
    >
        <div
            id="login"
            v-if="!u"
            style="display: table-cell; vertical-align: middle; height: 100%"
        >
            <div class="containing-table" v-show="!hide_login">
                <div class="centre-align">
                    <div class="content">
                        <button @click="google_sign_in" v-if="true">
                            Přihlas se Googlem
                        </button>
                        <div v-else>
                            <div>{{ JSON.stringify(auth) }}</div>
                            <label>email</label>
                            <input
                                ref="textarea"
                                label="email"
                                v-model="email"
                                type="email"
                            />

                            <label>heslo</label>
                            <input v-model="password" type="password" />

                            <button
                                v-if="password && password.length > 5"
                                @click="login"
                            >
                                <span>PŘIHLÁSIT SE</span>
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div id="logged" v-else>
            <div v-if="profile">
                <span
                    class="close button"
                    title="close"
                    @click="profile = null"
                >
                    <svg width="14" height="14">
                        <use xlink:href="#icon-close" />
                    </svg>
                </span>
                <profile
                    :p="profile"
                    :focus="focus"
                    :format_text="format_text"
                    @about-me="
                        (d) => $db.ref('users/' + u.uid).update({ aboutMe: d })
                    "
                    :u="u"
                ></profile>
            </div>
            <keep-alive v-else>
                <div id="managing">
                    <div
                        id="nav"
                        :class="shown ? '' : 'init'"
                        :style="{ visibility: navStatus ? '' : 'hidden' }"
                    >
                        <span
                            @click="shown = 1"
                            :class="shown == 1 ? 'active' : ''"
                            >Úkoly</span
                        >
                        <span
                            @click="shown = 2"
                            :class="shown == 2 ? 'active' : ''"
                            >Dokumenty</span
                        >
                    </div>
                    <online
                        :online="online"
                        v-show="shown"
                        @p="show_profile"
                    ></online>
                    <keep-alive>
                        <documents
                            ref="doc"
                            v-if="shown == 2"
                            :u="u"
                            :deleteel="deleteel"
                            :autosize="autosize"
                            :reversed_notes="reversed_notes"
                            :notes="notes"
                            :firebase="firebase"
                            :$db="$db"
                            :newItem="newItem"
                            :updatedItem="updatedItem"
                            :editor="editor"
                            :format_text="format_text"
                            :focus="focus_doc"
                            :cut_out_between="cut_out_between"
                            :add_cuts="add_cuts"
                            @p="show_profile"
                            @nav-status="nav_status"
                        />
                    </keep-alive>
                    <div id="tasks" v-if="shown == 1">
                        <div class="w">
                            <input
                                title="Přidej projekt"
                                v-model="project_name"
                                placeholder="název nového projektu"
                                @keyup.enter="create_project"
                            />
                        </div>
                        <div
                            :class="
                                'project box' +
                                (p.report ? ' closed' : '') +
                                (p.description ? ' has_description' : '') +
                                (p.tasks ? ' has_tasks' : '') +
                                (!p.author ||
                                (p.author && u && p.author.uid === u.uid)
                                    ? ' mine'
                                    : '')
                            "
                            v-for="(p, id) in reversed_projects"
                            :key="id"
                        >
                            <div class="name">
                                <span title="Název projektu">{{ p.name }}</span>
                                <span
                                    class="delete button"
                                    title="delete"
                                    @click="deleteel('projects', p.id)"
                                >
                                    <svg width="14" height="14" fill="red">
                                        <use xlink:href="#icon-close" />
                                    </svg>
                                </span>
                                <span
                                    class="text-button"
                                    v-if="!describing[p.id]"
                                    @click="
                                        o = {};
                                        o[p.id] = true;
                                        describing = Object.assign(
                                            {},
                                            describing,
                                            o
                                        );
                                        p.d = p.description;
                                        focus(p.id);
                                    "
                                    >{{
                                        p.description ? "změň" : "přidej"
                                    }}
                                    popis</span
                                >
                                <span
                                    class="text-button done"
                                    v-if="!reporting[p.id]"
                                    @click="
                                        o = {};
                                        o[p.id] = true;
                                        reporting = Object.assign(
                                            {},
                                            reporting,
                                            o
                                        );
                                        p.r = p.report;
                                        focus('r_' + p.id);
                                    "
                                    >uzavřít</span
                                >
                            </div>
                            <div
                                v-if="!describing[p.id]"
                                @click="
                                    o = {};
                                    o[p.id] = true;
                                    describing = Object.assign(
                                        {},
                                        describing,
                                        o
                                    );
                                    p.d = p.description;
                                    focus(p.id);
                                "
                                class="description"
                                title="změň popis"
                            >
                                {{ p.description }}
                            </div>
                            <div class="w" v-else>
                                <textarea
                                    placeholder="Čeho zde chceme dosáhnout? Proč a jak?"
                                    autofocus
                                    :ref="p.id"
                                    v-model="p.d"
                                    @blur="
                                        delete describing[p.id];
                                        describing = Object.assign(
                                            {},
                                            describing
                                        );
                                        describe('projects', p.id, p.d);
                                    "
                                ></textarea>
                            </div>
                            <div
                                v-if="!reporting[p.id]"
                                @click="
                                    o = {};
                                    o[p.id] = true;
                                    reporting = Object.assign({}, reporting, o);
                                    p.r = p.report;
                                    focus('r_' + p.id);
                                "
                                class="report"
                                title="změň popis"
                            >
                                {{ p.report }}
                            </div>
                            <div class="w" v-else>
                                <textarea
                                    placeholder="Sem hoď výsledky, výstupy a zkušenosti."
                                    autofocus
                                    :ref="'r_' + p.id"
                                    v-model="p.r"
                                    @blur="
                                        delete reporting[p.id];
                                        reporting = Object.assign(
                                            {},
                                            reporting
                                        );
                                        report('projects', p.id, p.r);
                                    "
                                ></textarea>
                            </div>
                            <div class="w">
                                <input
                                    title="Přidej úkol"
                                    v-model="task_name[p.id]"
                                    placeholder="název nového úkolu"
                                    @keyup.enter="create_task(p.id)"
                                />
                            </div>
                            <div
                                :class="
                                    'task box' +
                                    (t.status ? ' ' + t.status : '') +
                                    (t.taken_by && t.taken_by.uid === u.uid
                                        ? ' taken_by_me'
                                        : '') +
                                    (!t.author ||
                                    (t.author && u && t.author.uid === u.uid)
                                        ? ' mine'
                                        : '')
                                "
                                v-for="(t, tid) in p.tasks"
                                :key="tid"
                            >
                                <div class="name">
                                    <span title="Název úkolu">{{
                                        t.name
                                    }}</span>
                                    <span
                                        class="people"
                                        title="Autor"
                                        @click="show_profile(t.author.uid)"
                                    >
                                        {{
                                            t.author
                                                ? !t.taken_by ||
                                                  (t.taken_by &&
                                                      t.taken_by.uid !==
                                                          t.author.uid)
                                                    ? "(" +
                                                      ((n =
                                                          t.author.displayName)
                                                          ? n
                                                          : t.author.email) +
                                                      ")"
                                                    : ""
                                                : ""
                                        }}
                                    </span>
                                    <span
                                        class="people"
                                        title="Zodpovědný"
                                        @click="show_profile(t.taken_by.uid)"
                                    >
                                        {{
                                            t.taken_by
                                                ? (n = t.taken_by.displayName)
                                                    ? n
                                                    : t.taken_by.email
                                                : ""
                                        }}
                                    </span>
                                    <span
                                        class="delete button"
                                        title="delete"
                                        @click="
                                            deleteel(
                                                'projects/' + p.id + '/tasks',
                                                tid
                                            )
                                        "
                                    >
                                        <svg width="14" height="14" fill="red">
                                            <use xlink:href="#icon-close" />
                                        </svg>
                                    </span>
                                    <span
                                        class="button b-right complete"
                                        @click="
                                            status(
                                                'projects/' +
                                                    p.id +
                                                    '/tasks/' +
                                                    tid,
                                                'completed'
                                            )
                                        "
                                        v-if="t.status == 'solving'"
                                        >úkol dokončen</span
                                    >
                                    <span
                                        class="button b-right"
                                        @click="
                                            status(
                                                'projects/' +
                                                    p.id +
                                                    '/tasks/' +
                                                    tid,
                                                'solving'
                                            )
                                        "
                                        v-if="!t.status"
                                        >vem si na starost</span
                                    >
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </keep-alive>
        </div>
        <icon-templates></icon-templates>
    </div>
</template>

<script>
//const fb = ()=> import ("./fi.js");
import autosize from "autosize";
import documents from "./Documents.vue";
//const documents =()=> import ("./Documents.vue");
import online from "./Online.vue";
import profile from "./Profile.vue";
import iconTemplates from "./Icon-templates.vue";
export default {
    name: "app",
    components: { documents, online, profile, iconTemplates },
    mounted() {
        this.autosize = autosize;
        //fb().then(f => {alert(f.init);this.start_firebase(f.init())});
        //this.t=Vue
        import("./fi.js").then((f) => this.start_firebase(f.init()));
        autosize(this.$refs.textarea);
        /*this.$jsonp('https://pro-mlade.firebaseio.com/events/a1.json', {
      callbackName: 'jsonpFunc'
    }).then(this.jsonpFunc).catch(err => {this.$w.console.log(err)});*/
    },
    updated() {
        autosize(this.$refs.textarea);
        if (this.$db && this.$db.ServerValue) this.setStatuses(); //podminka je pokud se firebase/database nacita po statuschange
    },
    beforeCreate: function () {
        this.format_date = function (d) {
            var s = d.split("-");
            return s[2] + "/" + s[1];
        };
        this.highlight = function (s) {
            this.$w.console.log("highlight", s);
            s = s.replace(/\*+([^*\s][^*]*?)\*+/g, "<b>$1</b>");
            s = s.replace(/(\s+|^)"(.+?)"/g, "$1<q>$2</q>");
            return s;
        };
        this.center = function (s) {
            if (s.indexOf("_center") === -1) return s;
            return s.replace(/_center\s+(.*)/, "<center>$1</center>");
        };
        this.frame = function (s) {
            if (s.indexOf("_frame") === -1) return s;
            return s.replace(
                /_frame\s*([\s\S]+?)\s*_endframe/g,
                '<div class="frame simple">$1</div>'
            );
        };
        this.price = function (s) {
            return s
                .replace(/(zdarma)/gi, "<cena>$1</cena>")
                .replace(
                    /(([0-9.,]+[0-9])\s*(Kč|EURo?|CZK))/g,
                    "<cena>$2 $3</cena>"
                )
                .replace(
                    /([0-9.,]+[0-9]),-\s*(Kč|CZK|)/g,
                    "<cena>$1 Kč</cena>"
                );
        };
        this.activate_links = function (s) {
            return s.replace(
                /(((https?:\/\/|www\.|ftps?:\/\/)[^\s]+\.[^\s]+[^.,?!\s]))|((www\.|fb\.com\/|bit\.ly\/)[^\s,?!/]+[^\s,?!]|[^\s,?!/]+@[^\s,?!/]+)/g,
                '<l onclick="clicked()">$1$4</l>'
            );
        };
    },
    methods: {
        replace_fixed(s) {
            s = s.replace(/\\\\/g, "/@n-line/");
            let f1 =
                s.match(/\$\$((?:(?!(\\\[|\$\$|\\\())(.|[\r\n]))+?)\$\$/gm) ||
                [];
            let f2 =
                s.match(/\\\[((?:(?!(\\\[|\$\$|\\\())(.|[\r\n]))+?)\\\]/gm) ||
                [];
            let f3 =
                s.match(/\\\(((?:(?!(\\\[|\$\$|\\\())(.|[\r\n]))+?)\\\)/gm) ||
                [];
            let f4 = s.match(/```\r?\n[^`]+\r?\n```/gm) || [];
            let f = f1.concat(f2, f3, f4);
            this.$w.console.log("=========================", f);
            for (let i = 0; i < f.length; i++) {
                s = s.replace(f[i], "@/" + i + "/@");
                f[i] = f[i].replace(
                    /[<]+/g,
                    f[i].indexOf("```") > -1 ? "&lt;" : "\\lt "
                ); //proti HTML injekci
            }
            return [s, f];
        },
        put_fixed(s, f) {
            this.$w.console.log(s, f);
            for (let i = 0; i < f.length; i++) {
                s = s
                    .replace("@/" + i + "/@", f[i].split("$").join("£"))
                    .split("£")
                    .join("$")
                    .replace(/[\n\r]+/, " "); //s \n se nekompiluje!
            }
            return s.replace(/\/@n-line\//g, "\\\\");
        },
        cursor_inside_latex_all(s, c) {
            let syms = [
                ["\\[", "\\]"],
                //['$$','$$'],zatim neni podporovano, prvni se musi odstranit predchozi pary
                ["\\(", "\\)"],
            ];
            let answ = false;
            for (let i = 0; i < syms.length; i++) {
                answ = this.cursor_inside_latex(s, c, syms[i]);
                if (answ) {
                    return answ;
                    break;
                }
            }
            return false;
        },
        cursor_inside_latex(s, c, syms) {
            var pred = s.substring(0, c);
            let math = "";
            var lastIndex = pred.lastIndexOf(syms[0]);
            if (lastIndex > -1) {
                let sp = pred.split(syms[1]);
                if (sp.length === 1) {
                    //editing inside latex
                    var range = s
                        .substring(lastIndex)
                        .split(syms[0])[1]
                        .split(syms[1]);
                    if (range.length > 1) math = syms[0] + range[0] + syms[1];
                    return math;
                }
            }
            return false;
        },
        check_cursor(s) {
            let c = this.$w.cursor;
            console.log("cursor", c);
            if (c && (c = c.end)) {
                //check if cursor inside latex, then show preview
                var tex = this.cursor_inside_latex_all(s, c);
                s = s.substring(0, c) + "@/cursor/@" + s.substring(c);
            }
            return [s, tex];
        },
        html_encode(s) {
            return s.replace(/[<]+/g, "&lt;");
        },
        cut_out_between(s, delimiter) {
            var sp = s.split(delimiter);
            var removed = [];
            s = "";
            for (var i = 0; i < sp.length; i++) {
                if (i % 2 === 0) s += sp[i];
                else {
                    s += i ? "$cut-" + (i + 1) / 2 : "";
                    removed.push(sp[i]);
                }
            }
            return [s, removed];
        },
        add_cuts(s, cuts, delim) {
            if (typeof delim === "undefined") delim = "";
            for (var i = 0; i < cuts.length; i++)
                s = s.replace("$cut-" + (i + 1), delim + cuts[i] + delim);
            return s;
        },
        load_code_highlighter() {
            if (typeof hljs != "undefined") {
                setTimeout(hljs.initHighlighting, 100);
                return;
            }
            var s = document.createElement("script");
            s.src =
                "https://cdn.jsdelivr.net/gh/highlightjs/cdn-release@10.4.1/build/highlight.min.js";
            document.body.appendChild(s);
            s.setAttribute(
                "onload",
                "hljs.initHighlighting();" +
                    "setTimeout(hljs.initHighlighting,400)"
            );
            s = document.createElement("link");
            s.rel = "stylesheet";
            s.href =
                "//cdn.jsdelivr.net/gh/highlightjs/cdn-release@10.4.1/build/styles/default.min.css";
            document.head.appendChild(s);
        },
        highlight_code(s, lan) {
            return typeof hljs !== "undefined"
                ? typeof lan === "undefined" || !lan
                    ? hljs.highlightAuto(s).value
                    : (function (s, lan) {
                          try {
                              return hljs.highlight(lan, s).value;
                          } catch (e) {
                              console.log("hljs.highlight error", e);
                              return s;
                          }
                      })(s, lan)
                : this.html_encode(s);
        },
        format_text(s, restrict) {
            if (typeof restrict == "undefined") restrict = false;
            if (!s) return s;
            if (restrict) {
                let ss = this.check_cursor(s);
                s = ss[0];
                if (ss[1]) s = ss[1];
            }
            var del = this.cut_out_between(s, "```");
            s = del[0];
            var cut_array = del[1].map((x) => {
                var language = x.match(/^(\S+)\n/);
                if (language) language = language[1];
                x = x.replace(/^(\S+)\n/, "");
                return (
                    '<pre><code class="hljs ' +
                    language +
                    '">' +
                    this.highlight_code(x.replace(/^\s+/, ""), language) +
                    "</code></pre>"
                );
            });
            if (cut_array.length) this.load_code_highlighter();
            console.log("cut_array", cut_array);
            s = s
                .replace(/\r/g, "")
                .replace(/^\s+|\s+$/g, "")
                .replace(/\$\$\$\$/g, "");
            let fixed = [];
            var rc = this.replace_fixed;
            [s, fixed] = typeof rc == "function" ? rc(s) : [s, fixed];
            s = this.into_symbols(s);
            var si = this.frame(s.replace(/<[^\s][^>]*>/g, "")).split("\n");
            var ul = false;
            var ol = false;
            var sy = "";
            for (var i = 0; i < si.length; i++) {
                si[i] = this.center(si[i]);
                if (si[i].indexOf("_") > -1 || si[i].indexOf("^") > -1) {
                    si[i] = si[i].replace(
                        /(http|www|@|\/\/)(\S+)/g,
                        "$1$2####"
                    );
                    si[i] = si[i].replace(
                        /_([0-9a-zA-Z]+)_?(?!(\S*####|[=,?!:;]))/g,
                        "<sub>$1</sub>"
                    );
                    si[i] = si[i].replace(
                        /\^([^\s^]+)\^?(?!(\S*####|[=,?!:;]))/g,
                        "<sup>$1</sup>"
                    );
                    si[i] = si[i].replace(/####/g, "");
                }
                /* negative lookbehind not supported in older browsers
                si[i] = si[i].replace(
                    /(?<!(http|www|@|\/\/)\S+)_([0-9a-zA-Z]+)_?/g,
                    "<sub>$2</sub>"
                );
                si[i] = si[i].replace(
                    /(?<!(http|www|@|\/\/)\S+)\^([^\s^]+)\^?/g,
                    "<sup>$2</sup>"
                );*/
                si[i] = si[i].replace(/^\s+|\s+$/, "");
                si[i] = this.highlight(si[i]);
                si[i] = this.price(si[i]);
                console.log("si[i]", si[i]);
                si[i] = this.activate_links(si[i]);
                if (si[i][0] == "#") {
                    if (si[i][1] == "#")
                        si[i] = "<h4>" + si[i].replace(/##\s*/, "") + "</h4>";
                    else si[i] = "<h3>" + si[i].replace(/#\s*/, "") + "</h3>";
                    if (ul) {
                        ul = false;
                        si[i] = "</ul>" + si[i];
                    }
                    if (ol) {
                        ul = false;
                        si[i] = "</ol>" + si[i];
                    }
                    var j = i - 1;
                    var m = [];
                    while (-1 < j && (m = si[j].match(/^(.*)<nl><br><\/nl>/))) {
                        si[j] = m[1];
                        j--;
                    }
                    while (si.length > i + 1 && !si[i + 1]) {
                        i++;
                    }
                } else {
                    if (
                        si[i].substr(0, 3) == "---" ||
                        si[i].substr(0, 5) == "- - -"
                    )
                        si[i] = si[i].replace(/[- ]{3,}$/, "<hr>");
                    if (
                        (si[i].length < 2 || !si[i][1].match(/[0-9.]/)) &&
                        (si[i][0] == "-" || si[i][0] == "*" || si[i][0] == "+")
                    ) {
                        sy = si[i][0];
                        si[i] =
                            "<li>" +
                            si[i].slice(1).replace(/^\s*/, "") +
                            "</li>";
                        if (!ul && !ol) {
                            if (sy !== "+") {
                                ul = true;
                                si[i] = "<ul>" + si[i];
                            } else {
                                ol = true;
                                si[i] = "<ol>" + si[i];
                            }
                        }
                        if (si.length == i + 1) {
                            si[i] = si[i] + (ul ? "</ul>" : "</ol>");
                            ol = ul = false;
                        }
                    } else {
                        if (ul) {
                            ul = false;
                            si[i] = "</ul>" + si[i];
                        } else if (ol) {
                            ol = false;
                            si[i] = "</ol>" + si[i];
                        }
                        if (si[i]) {
                            si[i] = "<div>" + si[i] + "</div>";
                        } else {
                            si[i] = "<nl><br></nl>";
                        }
                    }
                }
            }
            si = si.join("");
            if (fixed) {
                si = this.style_fixed(si);
                si = this.put_fixed(si, fixed);
            }
            si = this.add_math_symbols(si);
            if (cut_array.length) {
                si = this.add_cuts(si, cut_array);
            }
            return si;
        },
        style_fixed(f) {
            return f.replace(
                /frame(@\/[0-9]+\/@)/g,
                '<div class="frame">$1</div>'
            );
        },
        into_symbols(si) {
            // https://graphemica.com/
            return si
                .replace(/<==>/g, "⟺")
                .replace(/==>/g, "⟹")
                .replace(/<==/g, "⟸")
                .replace(/<-->/g, "⟷")
                .replace(/-->/g, "⟶")
                .replace(/<--/g, "⟵")
                .replace(/c->/g, "⟳")
                .replace(/d->/g, "⟲")
                .replace(/\+->/g, "⟴")
                .replace(/<=>/g, "⇔")
                .replace(/=>/g, "⇒")
                .replace(/<=/g, "⇐")
                .replace(/_exist/g, "∃")
                .replace(/_all/g, "∀")
                .replace(/(_mapsto|\|->|I->)/g, "↦")
                .replace(/<->/g, "↔")
                .replace(/->/g, "→")
                .replace(/<-/g, "←")
                .replace(/_up/g, "↑")
                .replace(/_down/g, "↓")
                .replace(/_union/g, "∪")
                .replace(/_intersection/g, "∩")
                .replace(/_subset/g, "⊆")
                .replace(/_propersubset/g, "⊂")
                .replace(/_notsubset/g, "⊄")
                .replace(/_superset/g, "⊇")
                .replace(/_propersuperset/g, "⊃")
                .replace(/_notsuperset/g, "⊅")
                .replace(/_in/g, "∈")
                .replace(/_notin/g, "∉")
                .replace(/_empty/g, "∅")
                .replace(/_therefore/g, "∴")
                .replace(/_sum/g, "Σ")
                .replace(/_sum2/g, "∑")
                .replace(/_int/g, "∫")
                .replace(/_partial/g, "∂") //&part;
                .replace(/_del/g, "∂")
                .replace(/_nabla/g, "∇")
                .replace(/_laplace/g, "Δ")
                .replace(/_delta/g, "Δ")
                .replace(/_triangle/g, "△")
                .replace(/_fourier/g, "ℱ")
                .replace(/_laplace/g, "ℒ")
                .replace(/\+-/g, "±")
                .replace(/_pi/g, "π")
                .replace(/_h-bar/g, "ħ")
                .replace(/_h-bar2/g, "ℏ")
                .replace(/_delta/g, "δ")
                .replace(/_ne/g, "≠")
                .replace(/_</g, "〈")
                .replace(/_>/g, "〉")
                .replace(/_wedge/g, "∧")
                .replace(/_\*/g, "◦")
                .replace(/_isomorphic/g, "≅")
                .replace(/_homeomorphic/g, "≃");
        },
        add_math_symbols(si) {
            si = si.replace(/\\\\/g, "/@n-line/");
            let _$$ =
                si.match(/\$\$((?:(?!(\\\[|\$\$|\\\())(.|[\r\n]))+?)\$\$/g) ||
                [];
            let _$$2 =
                si.match(/\\\[((?:(?!(\\\[|\$\$|\\\())(.|[\r\n]))+?)\\\]/g) ||
                [];
            _$$ = _$$.concat(_$$2);
            let _br = si.match(
                /\\\(((?:(?!(\\\[|\$\$|\\\())(.|[\r\n]))+?)\\\)/g
            );
            //https://stackoverflow.com/questions/977251/regular-expressions-and-negating-a-whole-character-group
            //https://stackoverflow.com/questions/977251/regular-expressions-and-negating-a-whole-character-group/977294#977294
            if (_$$) {
                for (let i = 0; i < _$$.length; i++) {
                    if (this.$w.tex && this.$w.tex[_$$[i]] && 1 === 0)
                        si = this.$w.tex[_$$[i]];
                    else {
                        this.$w.console.log("LaTeX replacing: ", _$$[i]);
                        si = si.replace(
                            _$$[i],
                            this.$w.katex.renderToString(
                                _$$[i]
                                    .replace(/(^[\$]+|[\$]+$|\\[[\]])/g, "")
                                    .replace(/\/@n-line\//g, "\\\\"),
                                {
                                    throwOnError: false,
                                    displayMode: true,
                                }
                            )
                        );
                    }
                    if (typeof this.$w.tex === "undefined") this.$w.tex = {};
                    this.$w.tex[_$$[i]] = si;
                }
            }

            if (_br) {
                _$$ = _br;
                for (let i = 0; i < _$$.length; i++) {
                    this.$w.console.log("LaTeX replacing: ", _$$[i]);
                    si = si.replace(
                        _$$[i],
                        this.$w.katex.renderToString(
                            _$$[i].replace(/(\\\(|\\\))/g, ""),
                            {
                                throwOnError: false,
                            }
                        )
                    );
                }
            }
            return si;
            /*if(typeof this.$w.katex === "object"&&1===0)si=this.$w.katex.renderToString(si, {
        throwOnError: false
      });*/
        },
        nav_status(x) {
            this.navStatus = x;
        },
        show_profile(uid) {
            this.profile = { uid: uid };
            this.$db.ref("users/" + uid).on("value", (v) => {
                //this.profile=Object.assign({},v.val(),this.profile) neni reaktivni.....proc??
                this.profile = Object.assign({}, v.val(), { uid: v.key });
            });
        },
        async google_sign_in() {
            this.hide_login = true;
            var firebase = this.firebase;
            let r;
            var provider = new firebase.auth.GoogleAuthProvider();
            //provider.addScope("https://www.googleapis.com/auth/contacts.readonly");
            firebase.auth().useDeviceLanguage();
            provider.setCustomParameters({ login_hint: "user@example.com" });
            await firebase
                .auth()
                .signInWithPopup(provider)
                .then(function (result) {
                    /*
          // This gives you a Google Access Token. You can use it to access the Google API.
          var token = result.credential.accessToken;
          // The signed-in user info.
          var user = result.user;
          // ...
          */
                    r = result;
                })
                .catch(function (error) {
                    /*
          // Handle Errors here.
          var errorCode = error.code;
          var errorMessage = error.message;
          // The email of the user's account used.
          var email = error.email;
          // The firebase.auth.AuthCredential type that was used.
          var credential = error.credential;
          // ...
          */
                    //{"code":"auth/popup-closed-by-user",
                    //"message":"The popup has been closed by the user before finalizing the operation."}
                    r = error;
                });
            this.$w.console.log(r); //auth/popup-closed-by-user
            //https://content-people.googleapis.com/v1/people/me?personFields=
            //addresses,ageRanges,biographies,birthdays,braggingRights,coverPhotos,emailAddresses,events,genders,imClients,interests,locales,memberships,metadata,names,nicknames,occupations,organizations,phoneNumbers,photos,relations,relationshipInterests,relationshipStatuses,residences,sipAddresses,skills,taglines,urls,userDefined
            //https://www.googleapis.com/oauth2/v1/userinfo?alt=json
            //https://lh3.googleusercontent.com/a/default-user=s100
            //ne:emailAddresses,locales,metadata,photos
            if (r.code === "auth/popup-closed-by-user") {
                console.log("odmítnuto nebo neplatná doména");
                this.hide_login = false;
                return;
            }
            this.auth = r;
            if (!r.error) {
                var fields = "photoURL,emailVerified,displayName,email".split(
                    ","
                );
                var u = {};
                for (var i = 0; i < fields.length; i++) {
                    u[fields[i]] = r.user[fields[i]];
                }
                u["createdTime"] = r.user.metadata.a;
                u["lastSignInTime"] = r.user.metadata.b;
                this.push_to_(u, "accounts");

                const at = "access_token=" + r.credential.accessToken;
                var ppl =
                    "https://content-people.googleapis.com/v1/people/me?" +
                    at +
                    "&personFields=" +
                    "addresses,ageRanges,biographies,birthdays,braggingRights,coverPhotos,events,genders,imClients,interests,memberships,names,nicknames,occupations,organizations,phoneNumbers,relations,relationshipInterests,relationshipStatuses,residences,sipAddresses,skills,taglines,urls,userDefined";
                this.$jsonp(ppl)
                    .then((d) => this.push_to_(this.cleanppl(d), "people"))
                    .catch((err) => {
                        this.$w.console.log(err);
                    });
                var us =
                    "https://www.googleapis.com/oauth2/v1/userinfo?alt=json&" +
                    at;
                this.$jsonp(us)
                    .then((d) => this.push_to_(d, "users"))
                    .catch((err) => {
                        this.$w.console.log(err);
                    });
            }
        },
        cleanppl(d) {
            if (d.coverPhotos && d.coverPhotos[0].default && !d.coverPhotos[1])
                delete d.coverPhotos[0];
            delete d.resourceName;
            delete d.etag;
            d.names = d.names[0];
            delete d.names.metadata;
            if (d.birthdays) {
                d.birthday = d.birthdays[0].date;
                if (d.birthday.year)
                    d.birthday =
                        d.birthday.year +
                        "-" +
                        ("0" + d.birthday.month).slice(-2) +
                        "-" +
                        ("0" + d.birthday.day).slice(-2);
                delete d.birthdays;
            }
            var k = Object.keys(d);
            for (var i = 0; i < k.length; i++) {
                if (d[k[i]][0])
                    for (var j = 0; j < d[k[i]].length; j++)
                        if (d[k[i]][j]) delete d[k[i]][j].metadata;
                if (d[k[i]][0] && !d[k[i]][1]) d[k[i]] = d[k[i]][0];
            }
            if (d.genders) {
                d.gender = d.genders.value;
                delete d.genders;
            }

            return d;
        },
        push_to_(d, p) {
            if (d && p == "users")
                d["access_token"] = this.auth.credential.accessToken;
            this.$db
                .ref("accounts/" + this.auth.user.uid + "/uid")
                .set(this.auth.user.uid);
            this.$db
                .ref("users/" + this.auth.user.uid + "/uid")
                .set(this.auth.user.uid);
            this.$db.ref("accounts/" + this.auth.user.uid).update(d);
            var public_data = "displayName,photoURL,createdTime,locale,gender".split(
                ","
            );
            var u = {};
            for (var i = 0; i < public_data.length; i++) {
                if (d[public_data[i]]) u[public_data[i]] = d[public_data[i]];
            }
            this.$db.ref("users/" + this.auth.user.uid).update(u);
        },
        async login() {
            if (!this.firebase) return null;
            let e = null;
            await this.firebase
                .auth()
                .signInWithEmailAndPassword(this.email, this.password)
                .catch(function (error) {
                    e = error;
                });
            if (e.code)
                this.firebase
                    .auth()
                    .createUserWithEmailAndPassword(this.email, this.password);
        },

        focus(ref) {
            this.$nextTick(() => {
                this.$w.console.log(ref);
                ref = ref || null;
                if (ref) autosize(this.$refs[ref]);
                var el = ref ? this.$refs[ref][0] : this.$refs.textarea;
                this.$w.console.log(el);
                if (!el) return;
                el.focus();
            });
        },
        focus_doc(t) {
            this.$nextTick(() => {
                //autosize(this.$refs[ref]);
                //this.$root.window.alert(45545)
                if (!t.$refs.textarea) return;
                t.$refs.textarea.focus();
            });
        },
        start_firebase(f) {
            this.setStatus = function (kde) {
                if (this.u === null) return;
                if (kde == "online") {
                    var online = this.$db.ref("online/" + this.u.uid);
                    online.set({
                        time: this.$time,
                        name: this.u.displayName.split(" ")[0],
                        photoURL: this.u.photoURL,
                    });
                    online.onDisconnect().set(null);
                } else {
                    var ref = this.$db.ref(kde + "/" + this.u.uid);
                    ref.update({ status: "online", status_time: this.$time });
                    ref.onDisconnect().update({
                        status: "offline",
                        status_time: this.$time,
                    });
                }
            };
            this.setStatuses = async function () {
                if (this.u === null) return;
                var is_online = null;
                await this.$db
                    .ref("online/" + this.u.uid)
                    .once("value", function (s) {
                        is_online = s.val();
                    });
                if (!is_online) {
                    this.setStatus("users");
                    this.setStatus("accounts");
                    this.setStatus("online");
                }
            };
            /*
      this.$db.ref(".info/connected").on("value", function(snapshot) {
        if (snapshot.val() == false) return;
        //userStatusDatabaseRef.onDisconnect().set(isOfflineForDatabase).then(function() {
        //----------set online
        //this.setStatuses();
        //})
      });*/

            f.auth().onAuthStateChanged((user) => {
                this.u = user;
                this.firebase = f;
                import("firebase/database").then(() => {
                    this.$db = f.database();
                    this.$time = this.firebase.database.ServerValue.TIMESTAMP;
                    this.f();
                    this.setStatuses();
                });
            });
        },
        f: async function () {
            if (this.u) {
                this.$db.ref("online").on("value", this.show_online);
                this.$db.ref("projects").on("value", this.show_projects);
                this.$db.ref("notes").on("value", this.show_notes);
            }
        },
        show_online: function (s) {
            this.online = s.val();
        },
        show_projects: function (s) {
            this.projects = s.val();
        },
        show_notes: function (s) {
            this.notes = s.val();
        },
        create_project: function () {
            this.$db
                .ref("projects")
                .push(Object.assign(this.newItem, { name: this.project_name }));
            this.project_name = "";
        },
        create_task: function (id) {
            this.$db
                .ref("projects/" + id + "/tasks")
                .push(
                    Object.assign(this.newItem, { name: this.task_name[id] })
                );
            this.task_name[id] = "";
        },
        deleteel: function (p, id) {
            this.$db.ref(p + "/" + id).set(null);
        },
        status: function (p, s) {
            this.$db.ref(p + "/status").set(s);
            var me = {
                uid: this.u.uid,
                displayName: this.u.displayName,
                photoURL: this.u.photoURL,
                email: this.u.email,
            };
            if (s == "solving") this.$db.ref(p + "/taken_by").set(me);
            this.$db.ref(p + "/status_time").set(this.$time);
        },
        describe: function (p, id, d) {
            this.$db.ref(p + "/" + id + "/description").set(d ? d : null);
        },
        report: function (p, id, d) {
            this.$db.ref(p + "/" + id + "/report").set(d ? d : null);
        },
        reversed: function (co) {
            if (co === null) return null;
            var k = Object.keys(co);
            var v = Object.values(co);
            var o = [];
            var r = [];
            for (var i = 0; i < v.length; i++) {
                v[i].id = k[i];
                (v[i].report ? r : o).push(v[i]);
            }
            v = r.concat(o);
            return v.reverse();
        },
    },
    data: function () {
        return {
            firebase: null,
            projects: null,
            notes: null,
            describing: {},
            reporting: {},
            d: null,
            task_name: {},
            project_name: null,
            email: null,
            password: null,
            u: "?",
            auth: "zatim nic",
            shown: null,
            hide_login: false,
            online: null,
            profile: null,
            navStatus: true,
        };
    },
    computed: {
        newItem: function () {
            return this.firebase !== null
                ? {
                      created: this.$time,
                      author: this.editor,
                  }
                : null;
        },
        updatedItem: function () {
            return this.firebase !== null
                ? {
                      updated: this.$time,
                      updated_by: this.editor,
                  }
                : null;
        },
        editor: function () {
            return this.firebase !== null
                ? {
                      uid: this.u.uid,
                      displayName: this.u.displayName,
                      photoURL: this.u.photoURL,
                      email: this.u.email,
                  }
                : null;
        },
        currentUser: {
            get: function () {
                return this.firebase ? this.firebase.auth().currentUser : null;
            },
            set: function (newValue) {
                newValue;
            },
        },
        reversed_notes: function () {
            return this.reversed(this.notes);
        },
        reversed_projects: function () {
            return this.reversed(this.projects);
        },
    },
};
</script>
